import React, { useState, useEffect, useCallback, useMemo, memo } from 'react';

const RateTable = ({ 
  rates = [], 
  itemsPerPage = 10, 
  showAdmin = false,
  onDelete,
  onEdit,
  handleSort: externalHandleSort,
  showState = false,
  showDate = false,
  showPriceColumns = true,
  selectedCity = '',
  selectedState = '',
}) => {
  // State management
  const [currentPage, setCurrentPage] = useState(1);
  const [sortConfig, setSortConfig] = useState({ key: 'city', direction: 'ascending' });
  const [isTableVisible, setIsTableVisible] = useState(false);

  // Sort handler
  const handleSort = useCallback((key) => {
    if (externalHandleSort) {
      externalHandleSort(key);
      return;
    }
    setSortConfig((prevConfig) => ({
      key,
      direction: prevConfig.key === key && prevConfig.direction === 'ascending' ? 'descending' : 'ascending'
    }));
  }, [externalHandleSort]);

  // Sort rates with memoization
  const sortedRates = useMemo(() => {
    if (externalHandleSort) return rates;
    
    return [...rates].sort((a, b) => {
      const aVal = a[sortConfig.key];
      const bVal = b[sortConfig.key];
      
      if (sortConfig.direction === 'ascending') {
        return aVal < bVal ? -1 : aVal > bVal ? 1 : 0;
      }
      return aVal > bVal ? -1 : aVal < bVal ? 1 : 0;
    });
  }, [rates, sortConfig, externalHandleSort]);

  // Event handlers
  const handlePageClick = useCallback((pageNumber) => {
    setCurrentPage(pageNumber);
  }, []);

  // Pagination calculation
  const totalPages = Math.ceil((rates?.length || 0) / itemsPerPage);
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = sortedRates.slice(indexOfFirstItem, indexOfLastItem);

  // Hook to handle intersection observer
  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsTableVisible(true);
          observer.disconnect();
        }
      },
      { threshold: 0.1 }
    );

    const tableContainer = document.querySelector('.rate-table-container');
    if (tableContainer) {
      observer.observe(tableContainer);
    }

    return () => observer.disconnect();
  }, []);

  // Generate pagination controls
  const renderPaginationControls = useCallback(() => {
    const pages = [];
    for (let i = 1; i <= totalPages; i++) {
      pages.push(
        <button
          key={i}
          onClick={() => handlePageClick(i)}
          className={`px-3 py-1 mx-1 rounded ${
            currentPage === i
              ? 'bg-blue-600 text-white'
              : 'bg-gray-200 hover:bg-gray-300'
          }`}
          aria-label={`Page ${i}`}
        >
          {i}
        </button>
      );
    }
    return pages;
  }, [currentPage, totalPages, handlePageClick]);

  // SEO structured data
  const generateStructuredData = useCallback(() => {
    if (!selectedCity) return null;

    const localBusinessSchema = {
      "@context": "https://schema.org",
      "@type": "LocalBusiness",
      "name": `Egg Market ${selectedCity}`,
      "description": `Find today's egg rates in ${selectedCity}, ${selectedState}. Updated daily NECC egg prices and wholesale egg rates.`,
      "address": {
        "@type": "PostalAddress",
        "addressLocality": selectedCity,
        "addressRegion": selectedState,
        "addressCountry": "IN"
      },
      "url": `https://todayeggrates.com/${selectedCity.toLowerCase()}-egg-rate`,
      "aggregateRating": {
        "@type": "AggregateRating",
        "ratingValue": "4.8",
        "ratingCount": "254"
      },
      "priceRange": "₹₹",
      "offers": {
        "@type": "Offer",
        "itemOffered": {
          "@type": "Product",
          "name": "Eggs",
          "description": `Fresh eggs in ${selectedCity}, ${selectedState}`,
          "offers": {
            "@type": "Offer",
            "price": sortedRates[0]?.rate || 0,
            "priceCurrency": "INR",
            "availability": "https://schema.org/InStock",
            "priceValidUntil": new Date(new Date().setDate(new Date().getDate() + 1)).toISOString().split('T')[0]
          }
        }
      }
    };

    return (
      <script type="application/ld+json">
        {JSON.stringify(localBusinessSchema)}
      </script>
    );
  }, [selectedCity, selectedState, sortedRates]);

  // Early return for empty rates - after all hooks
  if (!rates || rates.length === 0) {
    return (
      <div className="text-center p-4">
        <p className="text-gray-600">No rates available at the moment.</p>
      </div>
    );
  }

  // Render table
  return (
    <div className="rate-table-container">
      {isTableVisible && (
        <>
          {generateStructuredData()}
          <div className="overflow-x-auto">
            <table className="min-w-full bg-white border-collapse">
              <thead>
                <tr className="bg-gray-50">
                  {showState && (
                    <th 
                      className="px-4 py-2 text-left cursor-pointer hover:bg-gray-100"
                      onClick={() => handleSort('state')}
                    >
                      State
                      {sortConfig.key === 'state' && (
                        <span className="ml-1">
                          {sortConfig.direction === 'ascending' ? '↑' : '↓'}
                        </span>
                      )}
                    </th>
                  )}
                  <th 
                    className="px-4 py-2 text-left cursor-pointer hover:bg-gray-100"
                    onClick={() => handleSort('city')}
                  >
                    City
                    {sortConfig.key === 'city' && (
                      <span className="ml-1">
                        {sortConfig.direction === 'ascending' ? '↑' : '↓'}
                      </span>
                    )}
                  </th>
                  {showPriceColumns && (
                    <>
                      <th 
                        className="px-4 py-2 text-left cursor-pointer hover:bg-gray-100"
                        onClick={() => handleSort('rate')}
                      >
                        Rate
                        {sortConfig.key === 'rate' && (
                          <span className="ml-1">
                            {sortConfig.direction === 'ascending' ? '↑' : '↓'}
                          </span>
                        )}
                      </th>
                      <th 
                        className="px-4 py-2 text-left cursor-pointer hover:bg-gray-100"
                        onClick={() => handleSort('retailPrice')}
                      >
                        Retail Price
                        {sortConfig.key === 'retailPrice' && (
                          <span className="ml-1">
                            {sortConfig.direction === 'ascending' ? '↑' : '↓'}
                          </span>
                        )}
                      </th>
                    </>
                  )}
                  {showDate && (
                    <th 
                      className="px-4 py-2 text-left cursor-pointer hover:bg-gray-100"
                      onClick={() => handleSort('date')}
                    >
                      Date
                      {sortConfig.key === 'date' && (
                        <span className="ml-1">
                          {sortConfig.direction === 'ascending' ? '↑' : '↓'}
                        </span>
                      )}
                    </th>
                  )}
                  {showAdmin && (
                    <th className="px-4 py-2 text-left">Actions</th>
                  )}
                </tr>
              </thead>
              <tbody>
                {currentItems.map((rate, index) => (
                  <tr 
                    key={rate.id || index}
                    className={index % 2 === 0 ? 'bg-white' : 'bg-gray-50'}
                  >
                    {showState && (
                      <td className="px-4 py-2">{rate.state}</td>
                    )}
                    <td className="px-4 py-2">{rate.city}</td>
                    {showDate && (
                      <td className="px-4 py-2">
                        {new Date(rate.date).toLocaleDateString()}
                      </td>
                    )}
                    {showPriceColumns && (
                      <>
                        <td className="px-4 py-2">₹{rate.rate}</td>
                        <td className="px-4 py-2">₹{(rate.rate * 30).toFixed(2)}</td>
                      </>
                    )}
                    {showAdmin && (
                      <td className="px-4 py-2">
                        <button
                          onClick={() => onEdit(rate)}
                          className="mr-2 text-blue-600 hover:text-blue-800"
                        >
                          Edit
                        </button>
                        <button
                          onClick={() => onDelete(rate.id)}
                          className="text-red-600 hover:text-red-800"
                        >
                          Delete
                        </button>
                      </td>
                    )}
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="flex justify-center mt-4">
            {renderPaginationControls()}
          </div>
        </>
      )}
    </div>
  );
};

export default memo(RateTable);